  -o-transition: all 1s linear;
  transition: all 1s linear;
  max-height: 0;
  overflow: hidden;
  font-size: 18px;
  padding-top: 10px; }
  .accordion p.open {
    max-height: 800px; }

.accordion h4 {
  font-weight: 300;
  padding: 20px 0;
  border-bottom: 2px solid #eeeeee;
  position: relative;
  cursor: pointer; }
  .accordion h4:before {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    left: -15px;
    content: url("https://about.crunchbase.com/wp-content/uploads/2021/01/caret-right-solid-2.svg");
